"use client";

import { useState, FormEvent } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Alert, AlertDescription } from "@/components/ui/alert";
import Navigation from "@/components/Navigation";

interface AnalysisResult {
  pair: string;
  timeframe: string;
  strategy: string;
  trend_structure?: string;
  entry_points?: string;
  stop_loss?: string;
  take_profit?: string;
  support_resistance?: string;
  notes?: string;
}

export default function HomePage() {
  const [pair, setPair] = useState("BTCUSD");
  const [timeframe, setTimeframe] = useState("1h");
  const [strategy, setStrategy] = useState("SMC");
  const [chartFile, setChartFile] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState<AnalysisResult | null>(null);
  const [error, setError] = useState("");

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    setError("");
    setResult(null);

    if (!chartFile) {
      setError("يرجى رفع صورة الرسم البياني.");
      return;
    }

    const formData = new FormData();
    formData.append("chart_image", chartFile);
    formData.append("pair", pair);
    formData.append("timeframe", timeframe);
    formData.append("strategy", strategy);

    try {
      setLoading(true);
      const res = await fetch("/api/analyze", {
        method: "POST",
        body: formData,
      });

      if (!res.ok) {
        const errData = await res.json();
        setError(errData.error || "حدث خطأ أثناء التحليل. يرجى المحاولة مرة أخرى.");
      } else {
        const data: AnalysisResult = await res.json();
        setResult(data);
      }
    } catch (err: any) {
      setError(err.message || "حدث خطأ في الاتصال.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100">
      <Navigation />
      <div className="max-w-4xl mx-auto p-4">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-slate-900 mb-2">
            Noro AI Trading Analysis
          </h1>
          <p className="text-slate-600 text-lg">
            تحليل الرسوم البيانية للتداول بواسطة الذكاء الاصطناعي
          </p>
        </div>

        {/* Main Content */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Analysis Form */}
          <Card className="shadow-lg">
            <CardHeader>
              <CardTitle className="text-xl text-slate-900">رفع الرسم البياني</CardTitle>
              <CardDescription>
                قم برفع صورة الرسم البياني واختر إعدادات التحليل
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-4">
                {/* File Upload */}
                <div className="space-y-2">
                  <Label htmlFor="chart" className="text-sm font-medium">
                    صورة الرسم البياني
                  </Label>
                  <Input
                    id="chart"
                    type="file"
                    accept="image/*"
                    onChange={(e) => setChartFile(e.target.files?.[0] || null)}
                    className="cursor-pointer"
                  />
                </div>

                {/* Trading Pair */}
                <div className="space-y-2">
                  <Label htmlFor="pair" className="text-sm font-medium">
                    زوج التداول
                  </Label>
                  <Input
                    id="pair"
                    type="text"
                    value={pair}
                    onChange={(e) => setPair(e.target.value)}
                    placeholder="مثل: BTCUSD, EURUSD"
                    className="text-center font-mono"
                  />
                </div>

                {/* Timeframe */}
                <div className="space-y-2">
                  <Label className="text-sm font-medium">الإطار الزمني</Label>
                  <Select value={timeframe} onValueChange={setTimeframe}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="1m">1 دقيقة</SelectItem>
                      <SelectItem value="5m">5 دقائق</SelectItem>
                      <SelectItem value="15m">15 دقيقة</SelectItem>
                      <SelectItem value="1h">ساعة واحدة</SelectItem>
                      <SelectItem value="4h">4 ساعات</SelectItem>
                      <SelectItem value="1D">يومي</SelectItem>
                      <SelectItem value="1W">أسبوعي</SelectItem>
                      <SelectItem value="1M">شهري</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                {/* Strategy */}
                <div className="space-y-2">
                  <Label className="text-sm font-medium">استراتيجية التحليل</Label>
                  <Select value={strategy} onValueChange={setStrategy}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="SMC">Smart Money Concepts</SelectItem>
                      <SelectItem value="Candlestick">تحليل الشموع اليابانية</SelectItem>
                      <SelectItem value="SupplyDemand">العرض والطلب</SelectItem>
                      <SelectItem value="TechnicalAnalysis">التحليل الفني</SelectItem>
                      <SelectItem value="PriceAction">حركة السعر</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                {/* Error Display */}
                {error && (
                  <Alert className="border-red-200 bg-red-50">
                    <AlertDescription className="text-red-700">
                      {error}
                    </AlertDescription>
                  </Alert>
                )}

                {/* Submit Button */}
                <Button
                  type="submit"
                  disabled={loading}
                  className="w-full bg-slate-900 hover:bg-slate-800 text-white"
                >
                  {loading ? "جار التحليل..." : "تحليل الرسم البياني"}
                </Button>
              </form>
            </CardContent>
          </Card>

          {/* Results Display */}
          <Card className="shadow-lg">
            <CardHeader>
              <CardTitle className="text-xl text-slate-900">نتائج التحليل</CardTitle>
              <CardDescription>
                {result ? "تحليل مفصل للرسم البياني" : "سيظهر التحليل هنا بعد رفع الصورة"}
              </CardDescription>
            </CardHeader>
            <CardContent>
              {result ? (
                <div className="space-y-4">
                  {/* Basic Info */}
                  <div className="grid grid-cols-3 gap-4 p-4 bg-slate-50 rounded-lg">
                    <div className="text-center">
                      <p className="text-sm text-slate-600">زوج التداول</p>
                      <p className="font-mono font-bold text-slate-900">{result.pair}</p>
                    </div>
                    <div className="text-center">
                      <p className="text-sm text-slate-600">الإطار الزمني</p>
                      <p className="font-semibold text-slate-900">{result.timeframe}</p>
                    </div>
                    <div className="text-center">
                      <p className="text-sm text-slate-600">الاستراتيجية</p>
                      <p className="font-semibold text-slate-900">{result.strategy}</p>
                    </div>
                  </div>

                  {/* Analysis Details */}
                  <div className="space-y-3">
                    {result.trend_structure && (
                      <div className="p-3 border-l-4 border-blue-500 bg-blue-50">
                        <h4 className="font-semibold text-blue-900 mb-1">الاتجاه والهيكل</h4>
                        <p className="text-blue-800 text-sm">{result.trend_structure}</p>
                      </div>
                    )}

                    {result.entry_points && (
                      <div className="p-3 border-l-4 border-green-500 bg-green-50">
                        <h4 className="font-semibold text-green-900 mb-1">نقاط الدخول</h4>
                        <p className="text-green-800 text-sm">{result.entry_points}</p>
                      </div>
                    )}

                    {result.stop_loss && (
                      <div className="p-3 border-l-4 border-red-500 bg-red-50">
                        <h4 className="font-semibold text-red-900 mb-1">وقف الخسارة</h4>
                        <p className="text-red-800 text-sm">{result.stop_loss}</p>
                      </div>
                    )}

                    {result.take_profit && (
                      <div className="p-3 border-l-4 border-yellow-500 bg-yellow-50">
                        <h4 className="font-semibold text-yellow-900 mb-1">جني الأرباح</h4>
                        <p className="text-yellow-800 text-sm">{result.take_profit}</p>
                      </div>
                    )}

                    {result.support_resistance && (
                      <div className="p-3 border-l-4 border-purple-500 bg-purple-50">
                        <h4 className="font-semibold text-purple-900 mb-1">الدعم والمقاومة</h4>
                        <p className="text-purple-800 text-sm">{result.support_resistance}</p>
                      </div>
                    )}

                    {result.notes && (
                      <div className="p-3 border-l-4 border-slate-500 bg-slate-50">
                        <h4 className="font-semibold text-slate-900 mb-1">ملاحظات إضافية</h4>
                        <p className="text-slate-700 text-sm">{result.notes}</p>
                      </div>
                    )}
                  </div>
                </div>
              ) : (
                <div className="text-center py-12 text-slate-500">
                  <div className="w-16 h-16 mx-auto mb-4 bg-slate-200 rounded-full flex items-center justify-center">
                    <span className="text-2xl">📊</span>
                  </div>
                  <p>قم برفع صورة الرسم البياني للحصول على التحليل</p>
                </div>
              )}
            </CardContent>
          </Card>
        </div>

        {/* Footer */}
        <div className="text-center mt-8 text-slate-500 text-sm">
          <p>تطبيق تحليل التداول بالذكاء الاصطناعي - Noro AI Clone</p>
        </div>
      </div>
    </div>
  );
}
